html,
body,
#root {
  height: 100%;
}

.color-picker-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  transition: background-color 0.2s;
}

.color-picker-component {
  margin-bottom: 20px;
}

.color-picker-row {
  font-size: 0;
}

.color-picker-cell {
  display: inline-block;
  width: 40px;
  height: 40px;
  border: 1px solid transparent;
  outline: 0;
  cursor: pointer;
}

.color-picker-cell:hover,
.color-picker-cell:focus,
.color-picker-cell:active {
  border-color: white;
  transform: scale(1.1);
}

.input-range {
  margin-bottom: 40px;
}

.input-range__track {
  height: 30px;
  border-radius: 15px;
  background: #e1451d;
  background: -moz-linear-gradient(left,#e1451d 0,#fdff47 17%,#86f9fe 50%,#2900f8 65%,#6e00f8 74%,#e33df9 83%,#e14423 100%);
  background: -webkit-gradient(linear,left top,right top,color-stop(0%,#e1451d),color-stop(17%,#fdff47),color-stop(50%,#86f9fe),color-stop(65%,#2900f8),color-stop(74%,#6e00f8),color-stop(83%,#e33df9),color-stop(100%,#e14423));
  background: -webkit-linear-gradient(left,#e1451d 0,#fdff47 17%,#86f9fe 50%,#2900f8 65%,#6e00f8 74%,#e33df9 83%,#e14423 100%);
  background: -o-linear-gradient(left,#e1451d 0,#fdff47 17%,#86f9fe 50%,#2900f8 65%,#6e00f8 74%,#e33df9 83%,#e14423 100%);
  background: -ms-linear-gradient(left,#e1451d 0,#fdff47 17%,#86f9fe 50%,#2900f8 65%,#6e00f8 74%,#e33df9 83%,#e14423 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#e1451d',endColorstr='#e14423',GradientType=1);
  background: linear-gradient(left,#e1451d 0,#fdff47 17%,#86f9fe 50%,#2900f8 65%,#6e00f8 74%,#e33df9 83%,#e14423 100%);
}

.input-range__track--active {
  background: transparent;
}

.input-range__label-container {
  display: none;
}

.input-range__slider {
  width: 1px;
  height: 30px;
  margin-top: -31px;
  margin-left: 0;
  border: 1px solid black;
  outline: 1px solid white;
  border-radius: 0;
  background: transparent;
}

input {
  box-sizing: border-box;
  width: 100%;
  text-align: center;
}
